<template>
  <div class="love">
    <div id="heard">
      <div id="cube">
        <div class="out-front">
          <img src="@/assets/1.jpg" alt="" />
        </div>
        <div class="out-back">
          <img src="@/assets/1.jpg" alt="" />
        </div>
        <div class="out-left">
          <img src="@/assets/1.jpg" alt="" />
        </div>
        <div class="out-right">
          <img src="@/assets/1.jpg" alt="" />
        </div>
        <div class="out-top">
          <img src="@/assets/1.jpg" alt="" />
        </div>
        <div class="out-bottom">
          <img src="@/assets/1.jpg" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {};
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      // 定义颜色
      // var colors = ["#FFB6C1", "#DB7093", "#FF69B4", "#483D8B","# 4169E1", "#87CEFA","#00BFFF", "#5F9EA0","#00FA9A", "#00FF7F","#7FFF00", "#FFFFE0", "#F5DEB3", "#BC8F8F", "#800000"];
      // 获取父元素
      var heard = document.getElementById('heard');
      // 循环出来  心的线条
      for (var i = 0; i < 36; i++) {
        // 创建一个线条
        var cdiv = document.createElement('div');
        // 为当前创建出来  元素添加class
        cdiv.className = 'heard';
        // 给当前创建出来div添加边框颜色
        // cdiv.style.border="2px solid "+colors[i%15];
        cdiv.style.border = '2px solid #e4393c';
        // 不存在的边框
        cdiv.style.borderLeft = '0';
        cdiv.style.borderBottom = '0';
        //旋转当前创建出来div 每一个div下标增加10deg
        cdiv.style.transform =
          'rotateY(' + i * 10 + 'deg) rotateZ(45deg) translateX(150px)';
        // 将创建出来的div追加到 当前父元素
        heard.appendChild(cdiv);
      }
    },
  },
};
</script>
